
.container {
    padding-left: 15px;
    padding-right: 9px;
    margin-left: auto;
    margin-right: auto;
        @media screen and (min-width: 481px) {
        width: 481px;
    }
        @media screen and (min-width: 768px) {
        width: 768px;
    }
        @media screen and (min-width: 1200px) {
        width: 1200px;
    }
}

a:link {
    text-decoration: none;
    padding: 0;
}

ul {
    list-style: none;
    padding-left: 0;
}

